หยาบและอาหารข้น อัตราส่วนระหว่างอาหารหยาบและอาหารข้นจะเป็นเท่าใดนั้นขึ้นอยู่กับ 1. ราคาของอาหารทั้งสองเปรียบเทียบกัน 2. อายุและสภาพของโค 3. ระยะเวลาของการขุน คือระยะต้นหรือระยะปลาย 4. จำนวนอาหารข้นที่ให้ ให้กินอย่างเต็มที่หรือกินอย่างจำกัด ถ้าให้กินอาหารข้นจำกัด จำนวนอาหารหยาบที่ใช้เลี้ยงโคต้องเพิ่มสูงขึ้นด้วย 5. อากาศ ถ้าอากาศร้อนควรให้อาหารข้นในปริมาณสูงขึ้น โดยปกติแล้วอัตราส่วนระหว่างอาหารหยาบต่ออาหารข้นสำหรับโคขุนควรเป็นดังนี้ คือ ระยะเริ่มต้น อาหารหยาบ : อาหารข้น = 70 : 30 ระยะกลางขุน อาหารหยาบ : อาหารข้น = 30 : 70 ระยะปลายของการขุน อาหารหยาบ : อาหารข้น = 15: 85 ปริมาณอาหารที่ให้โคขุน การให้อาหารที่ถูกต้องทั้งด้านปริมาณและโภชนะที่โคต้องการนั้น ต้องให้ตามความต้องการของโค ซึ่งจะมีการคำนวณและมีการใช้ตาราง ซึ่งจะต้องมีความรู้เรื่องนี้พอควร แต่ที่จะกล่าวต่อไปเป็นหลักเกณฑ์โดยปริมาณในการให้อาหารแก่โคขุนที่มีอายุ ต่างๆ กันคือ 1. โคมีอายุ 2 ปีขึ้นไป กินอาหารวัตถุแห้ง 2% ของน้ำหนักตัว 2. โคที่มีอายุ 1-2 ปี กินอาหารวัตถุแห้ง 2.5% ของน้ำหนักตัว 3. โคอายุต่ำกว่า 1 ปี กินอาหารวัตถุแห้ง 3% ของน้ำหนักตัว ตัวอย่าง โคตัวหนึ่งอายุ 2 ปี หนัก 240 ก.ก. ถูกนำมาขุนระยะหนึ่งโดยใช้อัตราส่วนระหว่างอาหารหยาบต่ออาหารข้น 30 : 70 อยากทราบว่าจะให้โคตัวนี้กินอาหารหยาบและอาหารข้นวันละเท่าใด โคกินอาหารวัตถุแห้ง (2% น้ำหนักตัว) = 2/100 X 240 = 4.80 ก.ก. ในอาหารหยาบจะมีวัตถุแห้ง = 4.80 X 30 / 100 = 1.44 ก.ก. ในอาหารข้นจะมีวัตถุแห้ง = 4.80 - 1.44 = 3.36 ก.ก. (โดยทั่วไปอาหารหยาบสดจะมีวัตถุแห้ง 20-25% มีความชื้น 75-80% ในที่นี้ขอใช้ 20%) วัตถุแห้งในอาหารหยาบสด 20 ก.ก. มาจากหญ้าสด 100 ก.ก. วัตถุแห้งในอาหารหยาบสด 1.44 ก.ก. มาจากหญ้าสด = (100 X 1.44) / 20 = 7.2 ก.ก. (โดยทั่วไปอาหารข้นที่ใช้เป็นอาหารสัตว์ จะมีความชื้นระหว่าง 12-15% ในที่นี้ขอใช้ 12%) อาหารข้นมีวัตถุแห้ง (100-12) 88 ก.ก. มาจากอาหารข้น = 100 อาหารข้นมีวัตถุแห้ง 3.36 ก.ก. มาจากอาหารข้น = (100 X 3.36) / 88 = 3.82 ก.ก. คำตอบ โคขุนตัวนี้ควรได้รับอาหารดังนี้ หญ้าสด วันละ = 7.20 ก.ก. อาหารข้น วันละ = 3.82 ก.ก. ")])])])])}]

