



























































import {
    Component,
    Vue,
} from 'vue-property-decorator';

import PoppularCattle from "@/components/GuideBook/PopularCattle.vue"
import Food from "@/components/GuideBook/Food.vue"
import Health from "@/components/GuideBook/Health.vue"
@Component({
    components: {
        PoppularCattle,
        Food,
        Health
    },
    computed: {},
    props: {
        color: {
            default: "light",
            validator: function (value) {
                return ["light", "dark"].indexOf(value) !== -1;
            },
        },
    }
})

export default class Table extends Vue {
    private openTab: number = 1
    private async created() {}
    toggleTabs(tabNumber: number) {
        this.openTab = tabNumber
    }

}
